import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import './details.css';

const url="https://ajrestaurantapp.herokuapp.com/restaurantDetails";

class RestaurantDetails extends Component{
    constructor(props){
         super(props)

         this.state = {
             rest:{
                "Cuisine":[
                    {"name":""},
                    {"name":""}
                ]
            }
         }
    }

    onBack = () => {
        let mealid= sessionStorage.getItem('type');
        console.log(mealid);
        this.props.history.push(`/list/${mealid}`)
    }

   

    render(){
        var {rest} = this.state;
         return(
            <div>
                <div>
                    <div className="panel-body" style={{borderColor:"rgb(236, 235, 235)"}}>
                        <div className="row">
                            <div className="col-md-12">
                                <img className="img-responsive photo" src={rest.thumb}/>
                            </div>
                            <div className="col-md-12">
                                <p className="rest-name">{rest.name}</p>
                                <p className="locality">{rest.locality}</p>
                                <p className="ad">{rest.address}</p>
                              
                            </div>
                     </div>
                     <br/><br/>

                     <Tabs>
                         <TabList>
                             <Tab style={{color:"#2654b8",fontFamily:"Poppins",fontSize:"15px",fontWeight:"600"}}>Overview</Tab>
                             <Tab style={{color:"#2654b8",fontFamily:"Poppins",fontSize:"15px",fontWeight:"600"}}>Contact</Tab>
                         </TabList>
                         <TabPanel>
                         <p className="Place">
                            About this place
                        </p>
                        <br/>
                        <p className="Cuisine">
                            Cuisine
                        </p>
                        <p className="Bakery-Fast-food">
                        {rest.Cuisine[0].name} | {rest.Cuisine[1].name}
                        </p>
                        <p className="Average-Cost">
                            Average Cost 
                            </p>
                        <p className="for-two-people-approx">
                            Rs.{rest.cost}  for two people(approx)
                        </p>
                         </TabPanel>
                         <TabPanel>
                         <p className="PhoneNumber">
                            Phone Number
                        </p>
                        <p className="Number">
                            +9114004566
                        </p>
                        <p className="Name">
                            Address
                        </p>
                        <p className="Address">
                            {rest.address}
                        </p>
                         </TabPanel>
                     </Tabs>
                    <br/><br/>
                    <div>
                        <button className="btn1" onClick={this.onBack}>Back</button>
                        &nbsp;
                        &nbsp;
                        <Link className="btn2" to={`/order/${this.props.match.params.id}`}>Place Order</Link>
                    </div>
                 </div>
             </div>
         </div>
         )
         
    }

    componentDidMount(){
        var restid=this.props.match.params.id;
        axios.get(`${url}/${restid}`)
        .then((response) => {this.setState( {rest:response.data[0]} )})
    }
}
 
export default RestaurantDetails;