import React, { Component } from 'react';
import './search.css';

const lurl="https://ajrestaurantapp.herokuapp.com/location";
const Rurl="https://ajrestaurantapp.herokuapp.com/restaurant?city="




class Search extends Component{
    constructor(){
        super()

        this.state={
            location:'',
            restaurents:''
        }
    }

   
    renderCity=(data)=>{
        if(data){
            return data.map((item) => {
                return(
                    <option value={item.city}>
                       {item.city_name}-{item.name}
                    </option>
                )
            })
        }
    }

    renderRestaurents=(data)=>{
        if(data){
            return data.map((item) => {
                return(
                    <option value={item._id}>
                       {item.name}|{item.locality}
                    </option>
                )
            })
        }
    }

    handleCity=(event)=>{
        console.log(event.target.value)
        fetch(`${Rurl}${event.target.value}`,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            this.setState({restaurents:data})
        })

    }

    handleRestaurent = (event) => {
       this.props.rid(event.target.value)
    }

    render(){
        console.log(this.state.location)
        return(
            <div className="imageContainer">
                <div id="logo">
                    e!
                </div>
                <div className="heading">
                    Find the best Restaurants, cafes, bars
                </div>
                <div className="locatioSelectors">
                    <select className="locationDropDown" 
                    onChange={this.handleCity}>
                        <option>Select City</option>
                        {this.renderCity(this.state.location)}
                    </select>
                    <select className="resturantsinput"
                    onChange={this.handleRestaurent} >
                        <option>Select Restaurant</option>
                        {this.renderRestaurents(this.state.restaurents)}
                    </select>
                </div>
    
            </div>
        )
    }

    componentDidMount(){
        fetch(lurl,{method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({location:data})
        })
       
    }
}



export default Search;