import React,{Component} from 'react';
import axios from 'axios';
import './filter.css';

const url="https://ajrestaurantapp.herokuapp.com/restaurantList";

class CuisineFilter extends Component{

    cuisineLogic = (event) => {
        let mealtype = sessionStorage.getItem('type');
        let cuisineType = event.target.value;
        let curl;
        if(cuisineType==""){
            curl = `${url}/${mealtype}`
        }else{
            curl = `${url}/${mealtype}?cuisine=${event.target.value}`
        }

        axios.get(curl)
        .then((response) => {this.props.restpercuisine(response.data)})
        
        console.log(curl)

    }

    render(){
        return(
            <div>
                <div class="Filters">Filters</div>
                 <div class="radio-header">Cuisine</div>
                    <div onChange={this.cuisineLogic}>
                            <div class="block">
                                <input type="checkbox" class="inline-block" name="cuisine" value=""/>
                                <span class="inline-block checkbox-option">All</span>
                            </div>
                            <div class="block">
                                <input type="checkbox" class="inline-block" name="cuisine" value="1"/>
                                <span class="inline-block checkbox-option">North Indian</span>
                            </div>
                            <div class="block">
                                <input type="checkbox" class="inline-block" name="cuisine" value="2"/>
                                <span class="inline-block checkbox-option">South Indian</span>
                            </div>
                            <div class="block">
                                <input type="checkbox" class="inline-block" name="cuisine" value="3"/>
                                <span class="inline-block checkbox-option">Chinese </span>
                            </div>
                            <div class="block">
                                <input type="checkbox" class="inline-block" name="cuisine" value="4"/>
                                <span class="inline-block checkbox-option">Fast Food</span>
                            </div>
                            <div class="block">
                                <input type="checkbox" class="inline-block" name="cuisine" value="5"/>
                                <span class="inline-block checkbox-option">Street Food</span>
                            </div>
                    </div>
            </div>
                    
            
        )
    }
}

export default CuisineFilter;