import React from 'react';
import {Link} from 'react-router-dom';

const Header = () =>{
    return(
        <nav className=" navbar-default navbar-inverse" style={{backgroundColor:"#ce0505",borderColor:"#ce0505",marginBottom:"15px"}}>
        <div className="container-fluid">
          <div className="navbar-header">
            <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
              <span className="icon-bar"></span>
            </button>
            <Link className="navbar-brand" to="/" style={{fontFamily:"Poppins",color:"white",fontSize:"25px"}}>Edumato</Link>
          </div>
          <div className="collapse navbar-collapse" id="myNavbar">
            <ul className="nav navbar-nav">
              <li ><Link to="/" style={{fontFamily:"Poppins",color:"white",fontSize:"18px"}}>Home</Link></li>
              <li><Link to="/vieworder" style={{fontFamily:"Poppins",color:"white",fontSize:"18px"}}>Bookings</Link></li>
              
            
            </ul>
           
          </div>
        </div>
      </nav>
    )
}




export default Header;


