import React,{Component} from 'react';
import axios from 'axios';
import './filter.css'

const url="https://ajrestaurantapp.herokuapp.com/restaurantList";

class CostFilter extends Component{

    costLogic = (event) => {
        let mealtype = sessionStorage.getItem('type');
        let cost = (event.target.value).split(',');
        let lcost = cost[0];
        let hcost = cost[1];
        let curl;
        if(event.target.value==""||event.target.value==""){
            curl=`${url}/${mealtype}`
        }else{
            curl=`${url}/${mealtype}?lcost=${lcost}&hcost=${hcost}`
        }

        axios.get(curl)
        .then((response)=>{this.props.restpercost(response.data)})
    }

    render(){
        return(
           <div>
               <div class="radio-header">Cost For Two</div>
                <div onChange={this.costLogic}>
                    <div class="block">
                        <input type="checkbox" class="inline-block" name="range" value=""/>
                        <span class="inline-block checkbox-option">All</span>
                    </div>
                    <div class="block">
                        <input type="checkbox" class="inline-block" name="range" value="100,300"/>
                        <span class="inline-block checkbox-option">100-300</span>
                    </div>
                    <div class="block">
                        <input type="checkbox" class="inline-block" name="range" value="301,400"/>
                        <span class="inline-block checkbox-option">301-400</span>
                    </div>
                    <div class="block">
                        <input type="checkbox" class="inline-block" name="range" value="401,500"/>
                        <span class="inline-block checkbox-option">401-500</span>
                    </div>
                    <div class="block">
                        <input type="checkbox" class="inline-block" name="range" value="501,1000"/>
                        <span class="inline-block checkbox-option">501-1000</span>
                    </div>
               </div>

           </div>
                
                           
                   
        
        )
    }
}

export default CostFilter;