import React from 'react';
import Search from './search';
import QuickSearch from './quickSearchApi';


const Home = (props) =>{

    const handleHotel = (data) => {
        props.history.push(`/rest/${data}`)
    }
    return(
        <div>
            <Search rid={(data)=>{console.log(handleHotel(data))}}/>
            <QuickSearch/>
        </div>
        
    )
}

export default Home;